/* main.scss - Main Stylesheet for Modern Web Project */
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
@import 'normalize.css/normalize.css';
/* _reset.scss - Modern Reset for Consistent Styling */
/* ====================================
   Box Sizing
   ==================================== */
/* Apply border-box to all elements for easier sizing */
*, *::before, *::after {
  box-sizing: border-box;
}

/* ====================================
   Margin and Padding
   ==================================== */
/* Remove default margin and padding from all elements */
* {
  margin: 0;
  padding: 0;
}

/* ====================================
   Typography
   ==================================== */
/* Set default font settings on the body */
html {
  font-size: 62.5%;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  color: #333;
  padding: 3rem;
  font-size: 1.6rem;
  line-height: 1.7;
  text-rendering: optimizeSpeed;
  /* Set tab size for consistent spacing */
  tab-size: 4;
}

/* Prevent line-height inheritance issues for form elements */
button, input, select, textarea {
  line-height: normal;
}

/* ====================================
   Links
   ==================================== */
/* Remove default styles from anchors */
a {
  text-decoration: none;
  color: inherit;
}

/* ====================================
   Media Elements
   ==================================== */
/* Ensure images and pictures are responsive */
img, picture {
  max-width: 100%;
  display: block;
}

/* Ensure videos and audios are responsive */
video, audio {
  display: block;
  max-width: 100%;
}

/* ====================================
   Forms
   ==================================== */
/* Inherit font styles for form elements */
input, button, textarea, select {
  font: inherit;
  border: 1px solid #ccc;
  outline: none;
}

/* Style buttons */
button {
  cursor: pointer;
  background: none;
  border: none; /* Overrides the border set above */
}

/* ====================================
   Lists
   ==================================== */
/* Remove list styles */
ul, ol {
  list-style: none;
}

/* Prevent text overflow */
h1, h2, h3, h4, h5, h6,
p, ul, ol, li, figure, blockquote, dl, dd {
  overflow-wrap: break-word;
}

/* ====================================
   Tables
   ==================================== */
/* Reset table styles */
table {
  border-collapse: collapse;
  width: 100%;
}

/* ====================================
   Accessibility
   ==================================== */
/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
    scroll-behavior: auto !important;
  }
}
@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
/* Fluid Root Font Size */
html {
  font-size: calc(62.5% + 0.25vw);
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #777;
  padding: 3rem;
  font-size: 1.6rem;
}

.header {
  position: relative;
  height: 95vh;
  background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url("../../../src/img/hero.jpg");
  background-size: cover;
  background-position: top;
  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
}
.header__logo-box {
  position: absolute;
  top: 4rem;
  left: 4rem;
  height: 3.5rem;
}
.header__logo {
  width: 100%; /* Makes the logo fill its container */
  max-width: 10rem; /* Caps the logo size to 10rem */
  height: auto;
  display: block;
}
.header__text-box {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.heading-main {
  color: #fff;
  text-transform: uppercase;
}
.heading-main--primary {
  display: block;
  font-size: 6rem;
  font-weight: 400;
  letter-spacing: 3.5rem;
  animation: moveInLeft 1s ease-out;
  backface-visibility: hidden;
}
.heading-main--secondary {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1.74rem;
  animation: moveInRight 1s ease-out;
}